* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content {
  height: 83vh;
}

.foot {
  color: aquamarine;
  height: 20vh;
  cursor: pointer;
}
.newform {
  background-color: #e9edef;
}
.navbar {
  height: 55px;
  background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
}

.navbar img {
  height: 30px;
  width: 30px;
  margin-right: 6px;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
}
.navbar button {
  background-image: radial-gradient(
    circle farthest-corner at 10% 20%,
    rgba(255, 209, 67, 1) 0%,
    rgb(224, 205, 61) 90%
  );
}
.navbar button:hover {
  background-image: radial-gradient(
    circle farthest-corner at 10% 20%,
    rgb(156, 143, 44) 0%,
    rgba(255, 145, 83, 1) 90%
  );
}

.use {
  color: #ccdee1;
  font-size: medium;
}

.use :hover {
  color: coral;
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}

.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 20px;
  background-image: linear-gradient(
    184.1deg,
    rgba(249, 255, 182, 1) 44.7%,
    rgba(226, 255, 172, 1) 67.2%
  );
}
.filters button :hover {
  background-image: linear-gradient(
    184.1deg,
    rgb(217, 235, 19) 44.7%,
    rgb(176, 238, 60) 67.2%
  );
}
.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.669);
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}
.active-icon {
  color: black;
}
.inactive-icon {
  color: grey;
}

.register-page {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to right, #c6ffdd, #fbd786, #f7797d);
}
.register-form {
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.326) 0px 3px 8px;
  padding: 20px;
  width: 30%;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.register-form h2 {
  margin-top: 10px;
  text-align: center;
  font-weight: bold;
  color: #b87333;
}

.register-form a {
  color: #b87333 !important;
}

.register-form a:hover {
  color: black !important;
}

.register-form button {
  background-color: #b87333;
  color: white;
}

.register-form input:focus {
  border: 1px solid #b87333 !important;
  color: #b87333;
}
.register-form button:hover {
  background-color: black;
  color: white;
}

.login-page {
  height: 100vh !important;
  width: 100vw !important;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.login-form {
  background-color: #b9bcccb6;
  margin-left: -15px;
  padding: 30px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.login-form h1 {
  text-align: center;
  font-weight: bold;
  margin: 30px;
  color: #344265;
}
.login-form .ant-form-item-label label {
  color: #344265 !important;
}
.login-form button {
  background-color: #344265;
  color: white;
  outline: none;
}
.login-form button:hover {
  background-color: black;
  color: white;
}
.login-form a {
  color: #344265 !important;
}

.login-form a:hover {
  color: black !important;
}
.link {
  text-decoration: none;
  margin-right: 6px;
  padding-top: 6px;
}
.login-page {
  background-image: url("login.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
